.piano {
    border: 5px solid #061f00;
    background-color: #00471b;
    width: 80vw;
    height: 290px;
    padding: 0px 25px 0 25px;
    position: relative;
    border-radius: 10px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
}
